import React, { Component } from 'react';
import quizQuestions from './api/quizQuestions';
import Quiz from './components/Quiz';
import Result from './components/Result';
import logo from './svg/logo.svg';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      counter: 0,
      questionId: 1,
      question: '',
      answerOptions: [],
      answer: '',
      answersCount: {},
      result: ''
    };

    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
  }

  componentDidMount() {
    const shuffledAnswerOptions = quizQuestions.map(question =>
      this.shuffleArray(question.answers)
    );
    this.setState({
      question: quizQuestions[0].question,
      answerOptions: shuffledAnswerOptions[0]
    });
  }

  shuffleArray(array) {
    var currentIndex = array.length,
      temporaryValue,
      randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }

  handleAnswerSelected(event) {
    this.setUserAnswer(event.currentTarget.value);

    if (this.state.questionId < quizQuestions.length) {
      setTimeout(() => this.setNextQuestion(), 300);
    } else {
      setTimeout(() => this.setResults(this.getResults()), 300);
    }
  }

  setUserAnswer(answer) {
    //divide the answer into two parts
    const answerArray = answer.split('-');
    const answerType = answerArray[0];
    const answerValue = answerArray[1];

    this.setState((state, props) => ({
      answersCount: {
        ...state.answersCount,
        [answerType]: (state.answersCount[answerType] || 0) + parseInt(answerValue)
      },
      answer: answer
    }));
  }
  

  setNextQuestion() {
    const counter = this.state.counter + 1;
    const questionId = this.state.questionId + 1;

    this.setState({
      counter: counter,
      questionId: questionId,
      question: quizQuestions[counter].question,
      answerOptions: quizQuestions[counter].answers,
      answer: ''
    });
  }

  getResults() {
    const eiResult = this.state.answersCount.ei;
    const snResult = this.state.answersCount.sn;
    const tfResult = this.state.answersCount.tf;
    const jpResult = this.state.answersCount.jp;
    
    const ei = eiResult >= 0 ? 'E' : 'I';
    const sn = snResult >= 0 ? 'S' : 'N';
    const tf = tfResult >= 0 ? 'T' : 'F';
    const jp = jpResult >= 0 ? 'J' : 'P';

    return ei + sn + tf + jp;
  }

  setResults(result) {
    if (result.length) {
      this.setState({ result: result });
    }

    //console.log(result);

  }

  renderQuiz() {
    return (
      <Quiz
        answer={this.state.answer}
        answerOptions={this.state.answerOptions}
        questionId={this.state.questionId}
        question={this.state.question}
        questionTotal={quizQuestions.length}
        onAnswerSelected={this.handleAnswerSelected}
      />
    );
  }

  renderResult() {
    //if result is intp
    if (this.state.result === 'INTP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is entp
    else if (this.state.result === 'ENTP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is infp
    else if (this.state.result === 'INFP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is enfp
    else if (this.state.result === 'ENFP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is istp
    else if (this.state.result === 'ISTP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is estp

    else if (this.state.result === 'ESTP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is isfp
    else if (this.state.result === 'ISFP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is esfp
    else if (this.state.result === 'ESFP') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is istj
    else if (this.state.result === 'ISTJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is estj
    else if (this.state.result === 'ESTJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is isfj
    else if (this.state.result === 'ISFJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is esfj
    else if (this.state.result === 'ESFJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is intj
    else if (this.state.result === 'INTJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is entj
    else if (this.state.result === 'ENTJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is infj
    else if (this.state.result === 'INFJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is enfj
    else if (this.state.result === 'ENFJ') {
      return (
        <Result quizResult={this.state.result} />
      );
    }
    //if result is undefined
    else {
      return (
        <Result quizResult={this.state.result} />
      );
    }

  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Animal type test</h2>
        </div>
        {this.state.result ? this.renderResult() : this.renderQuiz()}
      </div>
    );
  }
}

export default App;
